import config from "./config";
import axios from 'axios';
export default {
    getForm: function (id) {
        return axios.get(config.formUrl.trim() + id);
    },

    getForms: function (data) {
        return new Promise(function (resolve, reject) {
            data = data || {};
            data.filter = data.filter || {};
            data.filter.type = "form";
            data.filter = JSON.stringify(data.filter);

            if (data.sort) {
                data.sort = JSON.stringify(data.sort);
            }
            var url = config.formUrl.trim();
            var settings = {
                dataType: "json",
                url: url,
                method: "get",
                params: data
            };
            return axios(settings).then(function (response) {
                resolve(response.data._embedded);
            }).catch(function (error) {
                reject(error);
            });
        })
    },

    saveForm: function (form) {
        var url = config.formUrl.trim();
        var settings = {
            dataType: "text",
            url: url,
            headers: {
                "Content-Type": "application/json"
            },
            method: "post",
            data: form
        };
        return axios(settings);
    },
    deleteForm: function (id) {
        var url = config.formUrl.trim();
        url += id;
        var settings = {
            dataType: "text",
            url: url,
            method: "delete",
            data: {}
        };

        return axios(settings);
    }
}